<template>
    <div>
        <van-cell is-link @click="ipk()">密设置</van-cell>
        <van-cell is-link @click="showPopup">密码设置</van-cell>
        <van-cell is-link @click="showPopup">密码设置</van-cell>
<van-popup v-model="show" :style="{ height: '100%',width:'100%' }">
    <span class="tex">设置密码</span>
    <!-- 密码输入框 -->
<van-password-input
  :value="value"
  :focused="showKeyboard"
  @focus="showKeyboard = true"/>
  <!-- 数字键盘 -->
<van-number-keyboard
  v-model="value"
  :show="showKeyboard"
  @blur="showKeyboard = false"/>
</van-popup>
<van-cell title="选择单个日期" :value="date" @click="show = true" />
<van-calendar v-model="show" @confirm="onConfirm" />
    </div>
  </template>
  <script>
    export default{
        data(){
            return{
                value: '',
                showKeyboard: true,
                show: false,
                date: '',
            }
        },
        methods:{
          ipk(){
            this.$toast.loading({message: '加载中...',forbidClick: true,});
          },
            showPopup() {
                this.show = true;
            },
            formatDate(date) {
      return `${date.getMonth() + 1}/${date.getDate()}`;
    },
    onConfirm(date) {
      this.show = false;
      this.date = this.formatDate(date);
    },
        },
    }
</script>
  <style lang="less" scoped>
    .tex{
        font-size: 36px;
        font-weight: 900;
    }
  </style>
  